import React from "react";
import axios from "axios";
import Link from "next/link";

class Myzipcode extends React.Component {
  static async getInitialProps() {
    var promise = axios
      .get("http://localhost:4000/resonse")
      .then(response => {
        return {
          hasErrored: false,
          myzipcodeData: response.data
        };
      })
      .catch(error => {
        return {
          hasErrored: true,
          message: error.message
        };
      });
    return promise;
  }

  constructor(props) {
    super(props);
    this.state = {
      hasErrored: props.hasErrored,
      message: props.message,
      myzipcodeData: props.myzipcodeData
    };
  }

  componentDidMount() {}

  componentWillUnmount() {}

  render() {
    return (
      <div className="container dwi-div dwi-card">
        <div>
          <h5>Challenge #2</h5>
        </div>
        <div className="row">

          <div className="card-deck">
            <div className="card col-10 cardmin margintopbottom dwi-form-section">

  

              <div className="dwi-radio-items">
              {this.state.myzipcodeData.map((response) =>

                <div className="form-check"  key={myzipcode.id}>
                {myzipcode.city} 

                {/* "stateAbbrevation":"NY",
      "city":"Schenectady", */}


                </div>
              )}
              </div>
            </div>
          </div>
        </div>
      </div>
    );
  }
}

export default Myzipcode;
